<template>
    <header>
        <div class="wrapper">
            <el-row>
                <el-col :span="4">
                    <router-link to="/" class="logo" active-class="active" style="color: rgb(59, 76, 113);font-weight:bold;">
                        HevnWeb
                    </router-link>
                </el-col>
                <!--  -->
                <el-col :span="20">
                    <el-menu mode="horizontal" 
                        class="nav"
                        :default-active="$route.path" 
                        @select="hanleSelect" 
                        active-text-color="#139eff"
                        text-color="black" >
                        <!-- 搜索栏 -->
                        <el-menu-item class="input_out">
                                <el-input class="input" placeholder="请输入内容" v-model="keywards" size="medium" clearable>
                                    <el-button slot="append" icon="el-icon-search" @click="search()"> </el-button>
                                </el-input>
                        </el-menu-item>
                        <!--  -->
                        <el-menu-item index="/home" style="font-size:17px">
                            <router-link to="/home" active-class='active'>
                                <i class="iconfont icon-home"></i>首页
                            </router-link>
                        </el-menu-item>
                        <el-menu-item index="/github" style="font-size:17px">
                            <router-link to="/github" active-class="active" target="_blank">Github</router-link>
                        </el-menu-item>
                        <el-menu-item index="/about" style="font-size:17px">
                            <router-link to="/about" active-class='active'>About</router-link>
                        </el-menu-item>
                        <el-menu-item index="/article" style="font-size:17px" v-if="isSignIn===1">
                            <router-link to="/article" active-class='active'>我的博客</router-link>
                        </el-menu-item>
                        <el-menu-item index="/login" style="font-size:17px" v-if="isSignIn===0">
                            <router-link class="signBtn" to="/login" active-class='active'>登录</router-link>
                        </el-menu-item>
                        <el-menu-item index="/personal" v-else-if="isSignIn===1">
                            <router-link class="signBtn" to="/personal" active-class='active'>
                                <img class="nav_img" 
                                    style="width:30px;
                                    height: 30px;
                                    margin-left: 12px;
                                    border-radius: 50%;"
                                :src="userinfo.head_img==null?imgDefault:userinfo.head_img" alt="">
                                {{userinfo.nickname}}
                            </router-link>
                        </el-menu-item>
                    </el-menu>
                    
                </el-col>
            </el-row>
            
        </div>
    </header>
</template>

<script>
    import imgDefault from '@/assets/logo.jpg'

    export default {
        name:'CommonHeader',
        data() {
            return {
                userinfo: {},
                keywards: '',
                blogList:'',
                blog_list:[],
                imgDefault: imgDefault,
                isKey:1
            }
        },
        methods: {
            hanleSelect(index) {
                this.activeIndex = index
            },
            // 获取用户信息
            getUserInfo() {
                let isGet = this.$store.state.tokens.isSignIn
                if(isGet != 0){
                    this.$axios.get('/api/user/info')
                    .then((res) => {
                        let result = res.data
                        if (result.code === 0) {
                            this.userinfo = result.data
                        }
                    }).catch(e => {
                        console.log(e)
                    })
                }
            },
            // 获取全部博客列表
            getBlogList(){
                this.$axios.get('/api/article/allList')
                .then((res)=>{
                    this.blog_list = res.data.data;
                }).catch((e)=>{
                    console.log(e);
                })
            },
            //获取搜索文章
            getSearchList(){
                this.$axios.get('/api/article/search',{
                    params:{ keywards : this.keywards },
                })
                .then(res => {
                    if(res.data.code === 0){
                        this.blogList = res.data.data;
                    }
                }).catch(e=>{
                    console.log(e)
                })
            },
            search(){
                if(this.keywards!==''){
                    this.$store.commit('search/KEY',++this.isKey);
                    this.$router.push({
                        name:'search',
                        params:{
                            keywards:this.keywards
                        },
                    })
                }
                
            },

        },
        computed: {
            // 是否登录
            isSignIn() {
                return this.$store.state.tokens.isSignIn;
            }
        },
        watch:{
            keywards(val){
                this.keywards=val
            }
        },
        created() {
            this.getUserInfo();
            this.getBlogList();
        },
    }
</script>

<style lang="scss" scoped>

    header {
        // background: #2d2d2d;
        background: #fff;
        color: #9d9d9d;
        box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.5);
        // margin-bottom: 20px;
        .logo {
            line-height: 60px;
            font-size: 25px;
            font-weight: bold;
        }
        .nav {
            float: right;
            border-bottom: none;
            font-size: 25px;
            .router-link-active{
                background: skyblue;
                font-size: 40px;
            }
            li {
                padding: 0;
                a {
                    display: inline-block;
                    padding: 0 20px;
                    .iconfont {
                        vertical-align: top;
                        margin: 0 5px 0 0;
                    }
                }
            }
        }
    }
    // 修改链接高亮显示
    // ::v-deep .router-link-active{
    //     // background: skyblue;
    //     color: yellow !important;
    // }

    .signBtn {
        // background: #3b99fc !important;
        line-height: 60px;
    }
    // 搜索栏
    .input_out{
        margin-right: 30px;
    }
    ::v-deep .el-menu-item:hover {
        background-image: linear-gradient(135deg,#6bc30d,#30b8f5) !important;
        background-clip:text !important;
        -webkit-background-clip:text !important;
        color: transparent !important;
        background-color: transparent !important;
        font-size: 20px !important;
	}

</style>